import "./styles.css";
import React from "react";

export default function App() {
  const [amount, setamount] = React.useState(0);
  function handledeposit() {
    setamount(amount + 100);
  }
  const handlewithdraw = () => {
    if (amount > 0) {
      setamount(amount - 100);
    }
  };

  return (
    <div className="App">
      <h3>Wallet:₹ {amount}</h3>
      <button className="depo" onClick={handledeposit}>
        Deposit ₹100
      </button>
      <button className="with" onClick={handlewithdraw}>
        Withdraw ₹100
      </button>
    </div>
  );
}
